<template>
  <div>
    <vSearch
      @getOption="getOption"
      @searchInfo="searchInfo"
      @clearSearchInfo="clearSearchInfo"
      :pageType="pageType"
    ></vSearch>
    <el-table
      :data="tableData"
      style="width: 100%"
      max-height="675"
      :default-sort="{prop: 'date', order: 'descending'}"
    >
      <el-table-column
        prop="customerName"
        label="客户名称"
        sortable
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="customerId"
        label="客户号"
        sortable
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="money"
        label="放款金额"
        sortable
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="contractNum"
        label="合同编号"
        sortable
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="wareHouseId"
        label="对应仓单编号(仓单总数)"
        width="220"
      >
      </el-table-column>
      <el-table-column
        label="支付回单"
        width="110"
      >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'paymentReceipt')"
            type="text"
            size="small"
          >
            查看
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        label="增值税发票"
        width="110"
      >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'invoice')"
            type="text"
            size="small"
          >
            查看
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="prev, pager, next, jumper"
        :total="totalPage"
      >
      </el-pagination>
    </div>
    <el-dialog title="支付回单" :visible.sync="paymentReceiptVisible">
        <el-tabs type="border-card">
          <el-tab-pane label="支付回单">
            <div class="recordInfo">合同编号：{{11111}}</div>
            <div class="recordInfo">交易客户：{{2222222222222}}</div>
            <img src="" alt="支付回单" style="width:600px;height:300px;border:1px solid black">
            <div class="buttonGroup">
              <el-button type="primary">下载</el-button>
              <!-- <el-upload
                class="upload-demo"
                action="https://jsonplaceholder.typicode.com/posts/"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :before-remove="beforeRemove"
                multiple
                :limit="3"
                :on-exceed="handleExceed"
                :file-list="fileList">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload> -->
            </div>
          </el-tab-pane>
          <!-- <el-tab-pane label="增值税发票">
            <div class="recordInfo">合同编号：{{22222}}</div>
            <div class="recordInfo">交易客户：{{11111111111111}}</div>
            <img src="" alt="增值税发票" style="width:600px;height:300px;border:1px solid black;">
            <div class="buttonGroup">
              <el-button type="primary">下载</el-button>
              <el-upload
                class="upload-demo"
                action="https://jsonplaceholder.typicode.com/posts/"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :before-remove="beforeRemove"
                multiple
                :limit="3"
                :on-exceed="handleExceed"
                :file-list="fileList">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </div>
          </el-tab-pane> -->
        </el-tabs>
      </el-dialog>
      <el-dialog title="增值税发票" :visible.sync="invoiceVisible">
        <el-tabs type="border-card">
          <el-tab-pane label="增值税发票">
            <div class="recordInfo">合同编号：{{22222}}</div>
            <div class="recordInfo">交易客户：{{11111111111111}}</div>
            <img src="" alt="增值税发票" style="width:600px;height:300px;border:1px solid black;">
            <div class="buttonGroup">
              <el-button type="primary">下载</el-button>
            </div>
          </el-tab-pane>
        </el-tabs>
      </el-dialog>
  </div>
</template>

<script>
import vSearch from "../components/Search.vue";

export default {
  data() {
    return {
      searchForm: {
        option: "",
        inputText: ""
      },
      pageType: 5,
      paymentReceiptVisible: false,
      invoiceVisible: false,

      tableData: [
        {
          status: "1",
          customerId: "hz-hzjylxmyyxgs-180926",
          customerName: "杭州久益隆鑫贸易有限公司",
          processName: "仓单处理",
          wareHouseId: "al0260025533~al0260025548",
          money: "522583334",
          contractNum: "NHZB-CDZY-B-AL-180001",
          goodsType: "铝",
          price: "6,596,691.4",
          duration: "30",
          txTime: "2018-09-21",
          paymentDate: "2018-10-21"
        }
      ],

      currentPage: 1,
      pageSize: 1,
      totalPage: 100
    };
  },
  methods: {
    getOption(value, inputText) {
      this.searchForm.option = value;
      this.searchForm.inputText = inputText;
    },
    searchInfo() {
      console.log(this.searchForm.option + "+" + this.searchForm.inputText);
    },
    clearSearchInfo() {
      this.searchForm.option = "";
      this.searchForm.inputText = "";
    },
    formatter(row, column) {
      return row.address;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },

    //根据类型打开dialog
    openDialog(row, type) {
      if (type == "paymentReceipt") {
        this.paymentReceiptVisible = true;
      } else if (type == "invoice") {
        this.invoiceVisible = true;
      }
    }
  },
  components: {
    vSearch
  }
};
</script>

<style>
.pagination {
  text-align: center;
}
.buttonGroup {
  text-align: center;
  margin-left: -50px; 
}
</style>



